
.editor {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  margin-top: 60px;
}
.sidebar {
  position: absolute;
  width: 400px;
  top: 0;
  left: 0;
  padding: 10px;
}
.content {
  position: absolute;
  width: auto;
  margin-left: 30px;
  top: 0px;
  left: 410px;
}
textarea {
  box-sizing: border-box;
  font-size: 1em;
}
h2 {
  background: #FFF800;
  padding: 10px 25px;
}
h1 {
  background: #C50080;
  padding: 10px 25px;
  color: white;
}



